
import { defineComponent } from "vue";
import {
  selectedDeckGlobal,
  decks,
  deck,
  uniqueAllCards,
} from "@/components/global";
import {
  Card,
  CardType,
  Deck,
  getDecks,
  getDeck,
  setDecks,
  setDeck,
} from "@/API";
export default defineComponent({
  watch: { selectedDeckGlobal: "updateDeck" },
  setup() {
    return { selectedDeckGlobal, decks, deck, uniqueAllCards };
  },
  mounted() {
    this.updateDeck();
  window.addEventListener('click', event => {
      if (event.target == document.getElementById('cardAddEditModal')) {
        this.closeCardAddEditModal();
      }
      if (event.target == document.getElementById('cardDeleteModal')) {
        this.closeCardDeleteModal();
      }
    });
  },
  data() {
    return {
      allCards: [] as Card[],
      deckRating: 0,
      deckValue: 0,
      value: 0,
      deckNumber: 0,
      type: "monster" as CardType,

      properties: [false, false, false, false, false, false, false, false],
      counts: [0, 0, 0, 0, 0, 0, 0, 0],
      uniqueCounts: [0, 0, 0, 0, 0, 0, 0, 0],

      editCardId: 0,
      deleteCardId: 0,

      cardNameInput: "",
      cardCountInput: "",
      editAdd: "",
    };
  },
  methods: {
    updateDeck() {
      if (getDeck()) {
        this.deck = getDeck();
        this.uniqueCardDeck();
        this.countCard();
      }
    },
    editAddCard() {
      switch (this.editAdd) {
        case "add":
          this.addCard();
          break;
        case "edit":
          this.editCard();
          break;
      }
    },
    openCardDeleteModal(name: string) {
      this.deleteCardId = this.deck.sideCards.findIndex((c) => c.cardName == name);
      this.cardNameInput = this.deck.sideCards[this.deleteCardId].cardName;
      var modal = document.getElementById("cardDeleteModal");
      if (modal) modal.style.display = "block";
    },
    closeCardDeleteModal() {
      this.inputReset();
      var modal = document.getElementById("cardDeleteModal");
      if (modal) modal.style.display = "none";
    },
    openCardAddModal() {
      this.editAdd = "add";
      var modal = document.getElementById("cardAddEditModal");
      if (modal) modal.style.display = "block";
    },
    openCardEditModal(name: string) {
      this.editAdd = "edit";
      this.editCardId = this.deck.sideCards.findIndex((c) => c.cardName == name);
      this.cardNameInput = this.deck.sideCards[this.editCardId].cardName;
      this.cardCountInput = JSON.stringify(
        this.deck.sideCards[this.editCardId].cardCount
      );
      this.properties = this.deck.sideCards[this.editCardId].cardProperties;
      this.value = this.deck.sideCards[this.editCardId].cardValue;
      this.type = this.deck.sideCards[this.editCardId].cardType;
      var modal = document.getElementById("cardAddEditModal");
      if (modal) modal.style.display = "block";
    },
    closeCardAddEditModal() {
      this.inputReset();
      var modal = document.getElementById("cardAddEditModal");
      if (modal) modal.style.display = "none";
    },
    addCard() {
        if(this.deck.sideCards.length>2){
            if(this.deck.sideCards.map(c=>c.cardCount).reduce((a,b)=>a+b) +parseInt(this.cardCountInput)>15)return this.closeCardAddEditModal();
        }
        
      while (this.cardNameInput.endsWith(" "))
        this.cardNameInput = this.cardNameInput.slice(0, -1);
      if (this.deck.sideCards) {
        if (
          this.deck.sideCards.findIndex((c) => c.cardName == this.cardNameInput) ==
          -1
        ) {
          this.deck.sideCards.push({
            cardName: this.cardNameInput,
            cardType: this.type,
            cardCount: parseInt(this.cardCountInput),
            cardProperties: this.properties,
            cardValue: this.value,
          });
        }
        this.uniqueCardDeck();
        this.inputReset();
        this.countCard();
        this.sortDeck();
        this.safeDeck();
        this.closeCardAddEditModal();
      }
    },
    editCard() {
         if(this.deck.sideCards.map(c=>c.cardCount).reduce((a,b)=>a+b)-this.deck.sideCards[this.editCardId].cardCount+parseInt(this.cardCountInput)>15)return this.closeCardAddEditModal();
      while (this.cardNameInput.endsWith(" "))
        this.cardNameInput = this.cardNameInput.slice(0, -1);
      this.deck.sideCards[this.editCardId] = {
        cardName: this.cardNameInput,
        cardType: this.type,
        cardCount: parseInt(this.cardCountInput),
        cardProperties: this.properties,
        cardValue: this.value,
      };

      this.uniqueCardDeck();
      this.countCard();
      this.sortDeck();
      this.safeDeck();
      this.closeCardAddEditModal();
    },
    inputReset() {
      this.properties = [
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
      ];
      this.value = 0;
      this.editCardId = 0;
      this.deleteCardId = 0;
      this.type = "monster";
      this.cardNameInput = "";
      this.cardCountInput = "";
    },
    deleteCard(cardIndex: number) {
      this.deck.sideCards.splice(cardIndex, 1);
      this.countCard();
      this.safeDeck();
      this.closeCardDeleteModal();
    },
    countCard() {
      if (this.deck.sideCards) {
        this.deckNumber = 0;
        this.counts = [0, 0, 0, 0, 0, 0, 0, 0];
        this.uniqueCounts = [0, 0, 0, 0, 0, 0, 0, 0];
        for (let card of this.deck.sideCards) {
          this.deckNumber += card.cardCount;
          for (let c in this.counts) {
            if (card.cardProperties[c]) this.counts[c] += card.cardCount;
          }
        }
        for (let card of this.uniqueAllCards) {
          for (let c in this.uniqueCounts) {
            if (card.cardProperties[c]) this.uniqueCounts[c]++;
          }
        }
      }
    },
    uniqueCardDeck() {
      this.allCards = [];
      for (let card of this.deck.sideCards) {
        let count = card.cardCount;
        for (count; count; count--) {
          this.allCards.push(card);
        }
      }
      this.uniqueAllCards = [
        ...new Set(this.deck.sideCards.filter((c) => c.cardName)),
      ];
    },
    safeDeck() {
      this.decks[this.decks.findIndex((d) => d.name == this.deck.name)] =
        this.deck;
      setDeck(this.deck);
      setDecks(this.decks);
    },
    sortDeck() {
      this.deck.sideCards
        .sort(function (a, b) {
          if (a.cardName < b.cardName) {
            return -1;
          }
          if (a.cardName > b.cardName) {
            return 1;
          }
          return 0;
        })
        .sort(function (a, b) {
          return b.cardCount - a.cardCount;
        })
        .sort(function (a, b) {
          let map = {
            monster: 1,
            spell: 2,
            trap: 3,
          };
          return map[a.cardType] - map[b.cardType];
        });
    },
    loadDeck() {
      if (this.selectedDeckGlobal) {
        if (
          this.decks[
            this.decks.findIndex((d) => d.name == this.selectedDeckGlobal)
          ]
        ) {
          this.deck =
            this.decks[
              this.decks.findIndex((d) => d.name == this.selectedDeckGlobal)
            ];
        } else {
          this.decks.push({
            name: this.selectedDeckGlobal,
            cards: [],
            extraCards: [],
            combos: [],
            cardGroups: [],
            sideCards:[],
            alternativeCards:[],
          });
          this.deck =
            this.decks[
              this.decks.findIndex((d) => d.name == this.selectedDeckGlobal)
            ];
        }
        this.uniqueCardDeck();
        this.countCard();
        setDeck(this.deck);
      }
    },
  },
});
